    </div>

    <div class="col new">
      <div style="text-align: center;vertical-align: middle;padding-top: 45px;">
        <RouterLink to="/goalmaker" style="text-decoration:none;color: rgb(6,90,92);font-weight: 500;">
          <img src="../assets/icones/plus-circle-fill.svg" style="width:6em;text-align: center;" alt="join a new circle"
            class="aligncenter text-success filter-green"><span class="fs-1">&nbsp;&nbsp;새로운 목표 만들기</span>
        </RouterLink>

      </div>
    </div>
  </div>
  <br>


  <span class="fs-5">할 일&nbsp;&nbsp;&nbsp;&nbsp;
  </span>
  <span>오늘 할 일</span><span class="today">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>&nbsp;&nbsp;&nbsp;&nbsp;
  <span>내일 할 일</span><span class="tomorrow">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>&nbsp;&nbsp;&nbsp;&nbsp;
  <span>다음에 할 일</span><span class="later">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>

  <div class="littleAgenda table-responsive scrollableScreen" ref="scrollableScreen">
    <table class="table">
      <tbody>
        <tr>
          <td v-for="(list, i) in state.calLists" :key="(list, i)">
            <div class="today card" v-if="list.dates == getTodayString()">{{list.dates.substring(5)}}
              <p class="align-center">{{list.goalName}}</p>
              <p class="align-right">{{list.qdone}}/{{list.qgoal}}&nbsp;{{list.unit}}s</p>
            </div>
            <div class="tomorrow card" v-else-if="list.dates == getTomorrowString()">{{list.dates.substring(5)}}
              <p class="align-center">{{list.goalName}}</p>
              <p class="align-right">{{list.qdone}}/{{list.qgoal}}&nbsp;{{list.unit}}s</p>
            </div>
            <div class="later card tuto" v-else-if="list.goalName==1"> 등록된 목표가 없습니다.
              <p class="align-center ">목표를 설정해 보세요!</p>
              <p class="align-right">: )</p>
            </div>
            <div class="later card" v-else>{{list.dates.substring(5)}}
              <p class="align-center">{{list.goalName}}</p>
              <p class="align-right">{{list.qdone}}/{{list.qgoal}}&nbsp;{{list.unit}}s</p>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>

  <div class="CircleNewsSection row " style="overflow-y: scroll;height: 250px;">
    <section class="grid4Circle">
      <span v-for="(list, i) in state.newtaskdonedtoList" :key="(list, i)">
        <p hidden>{{state.lessThanFive++}}</p>

        <div class="aCircle">
          <div class="cTextArea titlecontent">

            <h5 class="align-center ">할일 완료!</h5>

            <p class="align-left"> {{list.circleName}} 에서</p>
            <p class="align-center">{{list.goalName}}</p>
          </div>
            <div>
            <!-- <UserIdCard class="inlineBlock" :dto="list" /> -->
          </div>
          <!-- <p class="align-right">{{list.qdone}}/{{list.qgoal}}&nbsp; {{list.unit}}s</p> -->
        </div>
      </span>

      <span v-for="(list, i) in state.newmemberlist" :key="(list, i)">
        <p hidden>{{state.lessThanFive++}}</p>

        <div class="aCircle">
          <div class="cTextArea">

            <h5 class="align-center ">새로운 멤버!</h5>
            <p class="align-center">{{list.circleName}} 에서</p>
          </div>
          <!-- <UserIdCard class="inlineBlock" :dto="list" /> -->

        </div>
      </span>

      <span v-for="(list, i) in state.newQuestionList" :key="(list, i)">
        <p hidden>{{state.lessThanFive++}}</p>

        <div class="aCircle">
          <div class="cTextArea">
            <p class="align-center fs-6 text-muted">{{list.circleName}} 에서</p>
            <h5 class="align-center">등록된 질문!</h5>

            <p class="align-center">{{list.questionTitle}}?</p>
          </div>
          <div>
            <!-- <UserIdCard class="inlineBlock" :dto="list" /> -->
          </div>

        </div>
      </span>
      <router-link to="circlemaker" style="text-decoration:none;">
        <span v-if="state.lessThanFive==0"  v-for="index in 1">
          <div class="aCircle">
            <img src="../assets/icones/plus-circle-fill.svg" style="width:2.5em;" 
              alt="join a new circle"
              class="aligncenter text-success filter-green"><br><br>
            <h5 class="align-center lightGrey" style="color:#06595b;"> 새로운 그룹 만들기</h5>
          </div>
        </span>
      </router-link>
      <span v-if="state.lessThanFive==0" v-for="index in 6">
        <div class="aCircle">
          <h5 class="align-center lightGrey">그룹 가입하고 새로운 소식을 받아보세요! :)</h5>
        </div>
      </span>
      <span v-else-if="state.lessThanFive==404" v-for="index in 5">
        <div class="aCircle">
          <h5 class="align-center lightGrey">그룹 가입하고 새로운 소식을 받아보세요! :)</h5>
        </div>
      </span>
      <span v-else-if="state.lessThanFive==303" v-for="index in 4">
        <div class="aCircle">
          <h5 class="align-center lightGrey">그룹 가입하고 새로운 소식을 받아보세요! :)</h5>
        </div>
      </span>
      <span v-else-if="state.lessThanFive==202" v-for="index in 3">
        <div class="aCircle">
          <h5 class="align-center lightGrey">그룹 가입하고 새로운 소식을 받아보세요! :)</h5>
        </div>
      </span>
      <span v-else-if="state.lessThanFive==101" v-for="index in 2">
        <div class="aCircle">
          <h5 class="align-center lightGrey">그룹 가입하고  새로운 소식을 받아보세요! :)</h5>
        </div>
      </span>
    </section>
  </div>
</template>
<script>
import { onMounted, ref } from 'vue'
import { reactive } from '@vue/reactivity'
import axios from 'axios';
import { useRouter } from 'vue-router';
import UserIdCard from '../components/UserIdCard.vue';

export default {

  ///!\-----------------

  ///!\-----------------

  setup() {
    const scrollableScreen = ref('')

    onMounted(() => { getGoalList(), calendarListCommandBoard(), scrollRight(), myCirclesNewsFeed() })
    const router = useRouter();
    const state = reactive({
      lists: '',
      calLists: '',
      goalDates: new Array(),
      today: new Date(),
      todays: new Array(),
      tomorrows: new Array(),
      laters: new Array(),
      lessThanFive: 0,
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    });
    const getGoalList = async () => {

      console.log("getGoalList launch")
      const url = "http://localhost:9090/api/goalList"

      const headers = {
        "Content-Type": "application/json",
        "Authorization": state.token,
        "token": state.token
      }
      const body = { email: sessionStorage.getItem("email") }
      await axios.post(url, body, { headers }).then(function (res) {
        state.lists = res.data
      }).catch((err) => { console.log("err :" + err) })
    }
    const getTodayString = () => {

      // Date.prototype.toISOString()
      // Converts a date to a string following the ISO 8601 Extended Format.  eg: 2020-07-10 15:00:00.000, 
      let today = new Date()
      let thedate = today.toISOString().substring(0, 10)
      return thedate
    }
    const getTomorrowString = () => {
      let today = new Date()
      let tomorrow = new Date()

      tomorrow.setDate(today.getDate() + 1)
      let thedate = tomorrow.toISOString().substring(0, 10)
      return thedate
    }
    const scrollRight = () => {
      scrollableScreen.scrollLeft = 0
    }
    const calendarListCommandBoard = async () => {

      console.log("calendarListCommandBoard launched")
      const url = "http://localhost:9090/api/calendarListCommandBoard"

      const headers = {
        "Content-Type": "application/json",
        "Authorization": state.token,
        "token": state.token
      }
      const body = { email: state.email }
      await axios.post(url, body, { headers }).then(function (res) {
        state.calLists = res.data
      }).catch((err) => { console.log(" calendarListCommandBoard err :" + err) })
    }
    const myCirclesNewsFeed = async () => {

      console.log("myCirclesNews launched");
      const url = "http://localhost:9090/api/mycirclesnewsfeed";

      const headers = {
        "Content-Type": "application/json",
        "Authorization": state.token,
        "token": state.token
      };
      const body = { email: state.email };
      await axios.post(url, body, { headers }).then((res) => {

        state.newtaskdonedtoList = res.data.newtaskdonedtoList;
        state.newmemberlist = res.data.newmemberlist;
        state.newQuestionList = res.data.newQuestionList;
      }).catch((err) => { console.log(" myCirclesNewsFeed err :" + err); });
    };
    return { scrollableScreen, myCirclesNewsFeed, state, getTodayString, getTomorrowString, getGoalList, calendarListCommandBoard, scrollRight, router }
  },
  components: { UserIdCard }

}
</script>
<style scoped>
.cTextArea {
  height: 100px;
}

.lightGrey {
  color: rgb(140, 140, 130);
}

.grid4Circle {
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
  grid-template-rows: 300px 300px 300px;
}

/* .littleAgenda{
  margin-top: 30px;
} */
.align-center {
  text-align: center;
  font-weight: 900;
  padding: 0px;
  padding: 0px;
}

.align-right {
  text-align: right;
  padding: 0px;
  padding: 0px;
}

/* .scrollableScreen {
  direction: rtl;
} */

.filter-green {
  filter: invert(26%) sepia(23%) saturate(2821%) hue-rotate(143deg) brightness(90%) contrast(95%);
}

.CircleNewsSection {
  margin-top: 50px;
}

.rightHandScrollBar {
  direction: rtl;
}

.aCircle {
  width: 200px;
  height: 200px;
  padding: 35px;
  border-radius: 180px;
  background-color: #D9D9D9;
  display: block;
  text-align: center;
}

.card {
  width: 200px;
  height: 110px;
  padding: 15px;
}

.new {
  background-color: aliceblue;
  border-radius: 2em;
  /* padding: 2em; */
  /* margin:2em; */
}

.goal {
  border-radius: 2em;
  margin-right: 2em;
  background-color: aliceblue;
}

.today {
  background-color: #e73c7e;
  border-radius: 2em;
}

.tomorrow {
  background-color: #ee7752;
  border-radius: 2em;
}

.later {
  background-color: #23a6d5;
  border-radius: 2em;
}

.tuto {
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 200% 200%;
  animation: gradient 10s ease infinite;
  color: aliceblue;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}


input:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6, 90, 92, 0.5);
}

textarea:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6, 90, 92, 0.5);
}
.titlecontent{
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
}
</style>