<template>
  <img v-if="!(dto!=null)" v-bind:src="state.img" class="img-thumbnail rounded-circle  " alt="대표이미지" />
  <img v-else src="../assets/images/pictures/profile_icon.png" class="img-thumbnail rounded-circle  " alt="대표이미지">

</template>
<script>
import { onMounted, ref, toRef } from 'vue'
import { reactive } from '@vue/reactivity'
export default {

  ///!\-----------------

  ///!\-----------------
  props: ['dto'],
  setup(props) {
    const dto = toRef(props, 'dto')
    onMounted(() => { showImage(dto); });
    const state = reactive({
      imgName: "",
      path: "",
      uuid: "",
      img: '',
      email: sessionStorage.getItem("email"),
      token: sessionStorage.getItem("TOKEN"),
    });

    const showImage = (dto) => {
        const url = `http://localhost:9090/display`
        let str = "";
        str = `${url}?fileName=${dto.thumbnailURL}`;
        state.img = str;     
      }

    return { showImage, state, dto }
  }
}
</script>
